import React from 'react';

/*
 * Fetch error helper
 *
 * @param {object} response
 */
export const handleResponse = (response) =>  response.json().then(json => response.ok ? json : Promise.reject(json))

/*
 * Render change percent helper
 *
 * @param {string} percent
 */
export const renderChangePercent = (percent) => {
    if (percent > 0) {
        return <span className="percent-raised">{percent}% &uarr;</span>
    } else if (percent < 0) {
        return <span className="percent-fallen">{percent}% &darr;</span>
    } else {
        return <span>{percent}%</span>
    }
}

/*
 * Calculate and return the start and end indices for a page
 *
 * @param {int} page
 * @param {int} perPage
 */
export const getIndices = (page, perPage) => {
    const startIndex = (page - 1) * perPage;
    const endIndex = startIndex + perPage;
    return [ startIndex, endIndex ];
}



