import React from 'react';
import ReactDom from 'react-dom';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import { API_URL, BASE_PATH } from './config';
import { getIndices, handleResponse } from './helpers';
import Header from './components/common/Header';
import List from './components/list/List';
import NotFound from './components/notFound/NotFound';
import Detail from './components/detail/Detail';

import './index.css';

class App extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            cache: {},
            currencies: [],
            error: null,
            loading: false,
            page: 1,
            totalPages: 0,
        };

        this.handleHeaderClick = this.handleHeaderClick.bind(this);
        this.handlePaginationClick = this.handlePaginationClick.bind(this);
        this.fetchCurrencies = this.fetchCurrencies.bind(this);
    }

    componentWillMount() {
        this.fetchAllCurrencies();
    }

    componentDidMount() {
        this.fetchCurrencies();
    }

    handleHeaderClick() {
        this.setState({ page: 1 }, () => this.fetchCurrencies()); 
    }

    handlePaginationClick = (direction) => {
        let currentPage = this.state.page;
        let nextPage = direction === 'next' ? currentPage + 1 : currentPage - 1;

        this.setState({ page: nextPage }, () => this.fetchCurrencies());
    }

    fetchAllCurrencies() {
        fetch(`${API_URL}/cryptocurrencies?all=true`)
            .then(handleResponse)
            .then(data => {
                const { currencies } = data;

                this.setState({
                    cache: {
                        allCurrencies: currencies
                    }
                })
            })
    }

    fetchCurrencies() {
        const { page } = this.state; 
        const perPage = 20;
        const hasAllCurrencies = !!this.state.cache.allCurrencies;
        
        this.setState({loading: true});


        if (hasAllCurrencies) {
            const { allCurrencies } = this.state.cache;
            const [ startIndex, endIndex ] = getIndices(page, perPage);
            const currencies = allCurrencies.slice(startIndex, endIndex);
            this.setState({
                loading: false,
                currencies,
            });
            return;
        }

        fetch(`${API_URL}/cryptocurrencies?page=${page}&perPage=20`)
            .then(handleResponse)
            .then((data) => {
                const { currencies, totalPages } = data;
                this.setState({
                    currencies,
                    totalPages,
                    loading: false
                });
            })
            .catch((error) => {
                this.setState({
                    error: error.errorMessage,
                    loading: false
                });
            });
    }

    render() {
        const { handlePaginationClick, handleHeaderClick } = this;
        const { page, loading, currencies, error, totalPages } = this.state;
        const props = { page, loading, currencies, error, totalPages, handlePaginationClick };

        return (
            <BrowserRouter>
                <div>
                    <Header onHeaderClick={handleHeaderClick} />
                    <Switch>
                        <Route path={`${BASE_PATH}`}
                            render={(routeProps) => (
                                <List { ...routeProps } { ...props } />
                            )}
                            exact />
                        <Route path={`${BASE_PATH}/:page`}
                               exact
                               render={(routeProps) => (
                                <List { ...routeProps } { ...props } />
                               )}
                        />
                        <Route path={`${BASE_PATH}/currency/:id`} component={Detail}></Route>
                        <Route component={NotFound}></Route>
                    </Switch>
                </div>
            </BrowserRouter>
            )
}
}

ReactDom.render(
    <App />,
    document.getElementById('root')
);



